function objectSlider() {
    var swiper = new Swiper('.object .swiper-container', {
        slidesPerView: 1,
        spaceBetween: 30,
        loop: true,
        centeredSlides: true,
        loopAdditionalSlides: 4,
        navigation: {
            nextEl: '.object .swiper-button-next',
            prevEl: '.object .swiper-button-prev',
        },
    })
}

objectSlider()
